$mobile-font-color : rgba(100, 100, 100, 1);
$mobile-wrapper-height : 600px;
$dark-purple: #3b0a65;
$light-purple: #c298e6;
$white: rgb(255, 255, 255);

body.index {
    background-size: cover;
    background-attachment: fixed;
    background-color: $light-purple;
    background-position: 0px 0px;
    background-repeat: no-repeat;
    overscroll-behavior-y: none;
    display: flex;
    align-items: center;
    justify-content: center; }

#Wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 12px 12px 2px 1px rgba(0, 0, 255, .2);
    margin-top: 170px;
    width: 500px;
    height: 100%;
    background-color: $white; }

#MainContent {
    position: absolute;
    top: 80px;
    right: 40px;
    left: 40px;
    width: 379.344px;
    padding-right: 5px;
    font-family: $main-font;
    color: #3b0a65;
    box-shadow: 2em 0em 0px gold;
    line-height: 15px;
    h1 {
        font-weight: 400;
        font-size: 32px;
        margin: 0.67em 0; }
    h2 {
        font-size: 18px;
        font-weight: 400;
        padding-bottom: 15px; }
    hr {
        left: 40px;
        width: 102%; }
    p {
        line-height: 1.675;
        font-size: 18px;
        font-weight: lighter;
        padding-bottom: 15px; }
    .fa {
        background-color: #333;
        border-radius: 30px;
        color: white !important;
        display: inline-block;
        height: 40px;
        width: 40px;
        line-height: 40px;
        margin: auto 15px auto auto;
        font-size: 15px;
        text-align: center; }
    .fa-facebook {
        background-color: #3B5998;
        &:hover {
            @include hoverShadow(rgba(59, 89, 152, .75)); } }
    .fa-twitter {
        background-color: #46c0fb;
        &:hover {
            @include hoverShadow(rgba(70, 192, 251, .75)); } }
    .fa-linkedin {
        background-color: #125688;
        &:hover {
            @include hoverShadow(rgba(18, 86, 136, .75)); } }
    .fa-github {
        background-color: #333;
        &:hover {
            @include hoverShadow(rgba(51, 51, 51, .75)); } }
    .fa-envelope {
        background-color: rgba(227, 125, 125, 1);
        &:hover {
            @include hoverShadow(#D19090); } } }

//media queries mobile
@media screen and (max-width: 480px) {
    body.index {
        overflow-x: hidden;
        background-size: cover;
        background-attachment: fixed;
        background-color: $light-purple;
        background-position: 0px 0px;
        background-repeat: no-repeat;
        width: inherit;
        #Wrapper {
            position: absolute;
            right: 10%;
            left: 10%;
            width: 80%;
            padding-top: 30%;
            height: $mobile-wrapper-height;
            background-color: $white; }
        #MainContent {
            margin: 0 auto;
            width: 70%;
            h1 {
                text-align: center;
                font-size: 30.5px;
                color: $mobile-font-color; }
            h2 {
                text-align: center;
                color: $mobile-font-color; }
            hr {
                left: 40px;
                width: 110%; }
            p {
                text-align: justify;
                text-align-last: justify;
                font-size: 15.5px;
                color: $mobile-font-color;
                padding-bottom: 15px; }
            .fa {
                margin-left: 7%; } } } }
